@import '@theme/vars.scss';

.slider {
    display: block;
    box-sizing: border-box;
    width: 100%;
    height: max-content;
    position: relative; 

    &__inner{
    }   

    &__navigation{
        box-sizing: border-box;
        pointer-events: none;
        display: flex;
        align-items: center;
        position: static;
        z-index: 2;
        margin: 0 -8px;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }


    &__button {
        pointer-events: all;
        cursor: pointer;
        margin: 0;
        box-sizing: border-box;
        position: static;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        margin: 0 8px;
        flex-shrink: 0;
        background-color: $light-light;
        fill: #101010;
        transition: background-color 0.35s ease, fill 0.35s ease, color 0.35s ease, opacity 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            width: 40px;
            height: 40px;
        }

        &--lg{
            width: 52px;
            height: 52px;
        }

        &:hover{
            background-color: $blue;
            fill: #fff;
        }

        &::after, 
        &::before{
            content: none;
        }

        &--transparent{
            background-color: transparent;
            border: 2px solid $light;
            fill: $light;

            &:hover{
                background-color: $light;
                fill: $blue;
            }
        }
    }

    &__button--lg &__button-icon {
        width: 16px;
        height: 16px;
        width: 24px;
        height: 24px;
    }

    &__button-icon{
        flex-shrink: 0;
        width: 16px;
        height: 16px;
    }
      
    &__pagination {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        width: 100%;
        z-index: 2;
        margin: 6px auto 0px;
        position: absolute;
        bottom: 8px;
        
        @media only screen and (max-width: $bp-lg) {
            bottom: 4px;
        }

        &.swiper-pagination-lock{
            display: none;
        }
    }
    

    &__pagination-item {
        cursor: pointer;
        display: flex;
        width: 12px;
        height: 6px;
        flex-shrink: 0;
        margin: 6px 3px 0;
        background-color: #D1D5DB;
        will-change: transform;
        transition: background-color 0.5s ease, width 0.35s cubic-bezier(.77,0,.175,1);

        @media only screen and (max-width: $bp-lg) {
            border-radius: 0px;
            height: 4px;
        }

        &.swiper-pagination-bullet-active{
            width: 22px;
            transition-delay: 0.35s;

            &::after{
                opacity: 1;
            }
        }

        
        &::after{
            content: "";
            width: 100%;
            height: 100%;
            background-color: $blue;
            position: absolute;
            left: 0;
            top: 0;
            transform-origin: left;
            opacity: 0;
            transition: opacity 0.5s ease 0.35s;
        }
    }

    &--autoplay .slider__pagination-item{

        &.swiper-pagination-bullet-active{
            width: 60px;
            background-color: $light;

            @media only screen and (max-width: $bp-lg) {
                width: 32px;
            }

            &::after{
                content: "";
                animation: fractionLine linear 8s infinite forwards;
            }
        }

        &::after{
            opacity: 1;
		    transform: scaleX(0%);
            animation: none;
        }
    }
}
@keyframes fractionLine {

	0%{ 
		transform: scaleX(0%);
	}
	100%{
		transform: scaleX(100%);
	}
}
