@import "@theme/vars.scss";


.labels{
    display: flex;
    align-items: center;
    overflow: hidden;

    &__list{
        display: flex;
        align-items: center;

        &:first-child{
            margin-left: -4px;
        }

        &:last-child{
            margin-right: -4px;
        }

        animation: labelsTranslate 10s linear forwards infinite;
        animation-play-state: paused;
    }

    &__item{
        position: relative;
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        height: 24px;
        padding: 0 8px;
        border-radius: 50px;
        border: 1px solid $orange;
        color: $orange;
        fill: $orange;
        text-decoration: none;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
        margin: 0 4px;
        max-width: 100%;
        overflow: hidden;
        flex-shrink: 0;
        transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease;

        &:hover:is(a){
            background-color: $orange;
            border-color: $orange;
            color: #fff;
            fill: #fff;
            
            div.icon{
                background-color: #fff;
            }
        }

        &:first-of-type{
            background-color: $orange;
            color: #fff;
            fill: #fff;

            div.icon{
                background-color: #fff;
            }

            &:hover:is(a){
                background-color: #E53A00;  
                border-color: #E53A00;  
            }
        }
        
        div.icon{
            background-color: $orange;
        }

        .icon{
            width: 16px;
            height: 16px;
            margin-right: 5px;
            mask-size: 100%;
            mask-repeat: no-repeat;
            transition: background-color 0.35s ease;
        }

        span{
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 100%;
            white-space: nowrap;

        }

    }
}

@keyframes labelsTranslate {
    0%{
        transform: translateX(0%);
    }

    100%{
        transform: translateX(-100%);
    }
}